import { Add, Remove } from "@material-ui/icons";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useContext } from "react";
import { Link } from "react-router-dom";
import { AuthContext } from "../../context/AuthContext";
import { Users } from "../../dummyData";
import Online from "../online/Online";
import "./Rightbar.css";

const Rightbar = ({ user }) => {

  console.log(user)

  const PF = "http://localhost:5000/images/";
  const [friends, setFriends] = useState([])
  const {user : currentUser,dispatch} = useContext(  AuthContext)
  const followed = currentUser.followings.includes(user?._id)
  
  useEffect(() => {
    console.log(user);
    const getFriends = async () => {
      try {
        const friendList = await axios.get("http://localhost:5000/api/users/friends/" + user?._id)
        setFriends(friendList.data)
      } catch (error) {
        console.log(error)
      }
    }
    getFriends()
    // setFollowed(currentUser.followings.includes(user._id))
  }, [user])

  const handleClick = async () => {
    console.log('first')
    try {
      if(followed) {
        await axios.put("http://localhost:5000/api/users/" + user._id + "/unfollow",{
          userId : currentUser._id
        })
        dispatch({
          type : "UNFOLLOW",
          payload : user._id
        })
      }
        else{
          await axios.put("http://localhost:5000/api/users/" + user._id + "/follow",{
            userId : currentUser._id
          })
          dispatch({
            type : "FOLLOW",
            payload : user._id
          })
        }
        followed = !followed
    } catch (error) {
      console.log(error)
    }
  }
  

  const HomeRightbar = () => {
    return (
      <>
        <div className="birthdayContainer">
          <img className="birthdayImg" src={`${PF}gift.png`} alt="" />
          <span className="birthdayText">
            <b>Pola Foster</b> and <b>3 other friends</b> have a birhday today.
          </span>
        </div>
        <img className="rightbarAd" src={`${PF}ad.png`} alt="" />
        <h4 className="rightbarTitle">Online Friends</h4>
        <ul className="rightbarFriendList">
          {Users.map((u) => (
            <Online key={u.id} user={u} />
          ))}
        </ul>
      </>
    );
  };

  const ProfileRightbar = () => {
    return (
      <>
      {user.username !== currentUser.username && (
        <button className="rightbarFollowButton" onClick={handleClick}>
          {followed ? "Unfollow" : "Follow"}
          {followed ? <Remove /> : <Add />}
        </button>
      )}
        <h4 className="rightbarTitle">User information</h4>
        <div className="rightbarInfo">
          <div className="rightbarInfoItem">
            <span className="rightbarInfoKey">City:</span>
            <span className="rightbarInfoValue">{user.city}</span>
          </div>
          <div className="rightbarInfoItem">
            <span className="rightbarInfoKey">From:</span>
            <span className="rightbarInfoValue">{user.from}</span>
          </div>
          <div className="rightbarInfoItem">
            <span className="rightbarInfoKey">Relationship:</span>
            <span className="rightbarInfoValue">
              {user.relationship === 1
                ? "Single"
                : user.relationship === 2
                ? "Married"
                : "Unknown"}
            </span>
          </div>
        </div>
        <h4 className="rightbarTitle">User friends</h4>
        <div className="rightbarFollowings">
        {friends.map((friend) => (
          <Link to={"/profile/"+friend.username} style={{textDecoration : "none"}}>
          <div className="rightbarFollowing">
          <img
            src={friend.profilePicture ? friend.profilePicture : PF+"person/noAvatar.png"}
            alt=""
            className="rightbarFollowingImg"
          />
          <span className="rightbarFollowingName">{friend.username}</span>
        </div>
        </Link>
        ))}
        </div>
      </>
    );
  };
  return (
    <div className="rightbar">
      <div className="rightbarWrapper">
        {user ? <ProfileRightbar /> : <HomeRightbar />}
      </div>
    </div>
  );
};

export default Rightbar;
